:root{
    /*COLORS*/    
    --gbg: rgb(197, 197, 197); 
    --dgbg: #1d1b19;
    --dc: #987750;
    --gc: #888;
    --wc: #ddd;  
    --grc: rgb(54, 182, 37);  
    --rc : rgb(151 39 39);
    --white:rgb(255, 255, 255);
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Jost', sans-serif;  
    color: var(--gc);
    font-weight: 400;
}

body{         
    background-color: var(--dgbg);
}

li{                             
    list-style: none;
}

.key{                             
    font-size: 18px;
    letter-spacing: 1px;
    color: var(--gc);
    font-weight: 500;
}

.value{                           
    font-size: 18px;
    letter-spacing: 0;
    color: var(--wc);
    font-weight: 400;
}


h1, h2, h3, h4, h5, h6 {
    line-height: 1.2;
    font-weight: 600;
    color: var(--wc);
}

h2{                             
    font-size: 45px;
    font-weight:400;
}

h3{
    font-size: 30px;
}

/* h4{
    
} */

h5{                               
    font-size: 18px;
    font-weight: 400;
    line-height: 30px; 
}


p{
    font-size: 20px;
    line-height: 44px;
    letter-spacing: 0;
    color: var(--gc);
}

.GOLD{                      
    font-weight: 500;
    letter-spacing: 1px;
    color: var(--dc);
}

.sign{
    font-family: 'Smooch', cursive;
    font-size: 3rem;
    margin: 2rem;
}

.flex{
    display:flex;
}
.flex-align-center{
    display: flex;
    align-items: center;
}
.flex-centerAll{
    display:flex;
    align-items: center;
    justify-content:center;
}
