  textarea {
    width: 650px;
    max-width: 670px;
    height: 200px;
    max-height: 260px;
    padding: 25px;
    margin: 10px 0 30px 0;
    background: transparent;
    outline: none;
    
    color: #726659;
    
    font-size: 0.875em;
    
    border: 1px solid rgba(255,255,255,.2);
    
    transition: .3s;
    
  }
  
  textarea:hover {
    border: solid 1px #b3aca7;
    
  }
  
  #submit {
    width: 650px;
    padding: 0;
    margin: -5px 0px 0px 0px;
    background: transparent;
    font-size: 0.875em;
    border: 2px solid rgba(255,255,255,.2);
    color: var(--wc);
    outline:none;
    cursor: pointer;
    
  }
  
  #submit:hover {
    border: solid 1px var(--dc);
    color: var(--dc); 
  }

